<template>
  <div>
    <div class="title">
        绑定
    </div>
    <div class="bindingBox">
      <div class="leftBDing">
        <div class="titBding">
          用户
          <el-divider></el-divider>
        </div>
          <!-- 这是输入筛选框 -->
          <el-input
            placeholder="输入关键字进行过滤"
            v-model="filterText">
          </el-input>
          <el-tree
            class="filter-tree"
            :data="data"
            :props="defaultProps"
            default-expand-all
            :filter-node-method="filterNode"
            ref="tree">
          </el-tree>
          <!-- 树状图结束 -->
      </div>
      <div class="rigthBDing">
        <div class="titBding">
          设备
          <el-divider></el-divider>
        </div>
        <!-- 没有绑定的设备 -->
        <div class="Nobinding">
          未绑定的设备
          <el-divider></el-divider>
          <!-- 搜索 -->
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="序列号">
              <el-input v-model="formInline.user" placeholder="支持模糊查询"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
          </el-form>
          <!-- 列表 -->
          <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column
              label="日期"
              width="120">
              <template slot-scope="scope">{{ scope.row.date }}</template>
            </el-table-column>
            <el-table-column
              prop="name"
              label="姓名"
              width="120">
            </el-table-column>
            <el-table-column
              prop="address"
              label="地址"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
          </el-table>
        </div>
        <!-- 双向按钮 -->
        <div class="twoWay">
          <el-button type="primary" class="btn">
            <svg class="icon svg">
              <use xlink:href="#icon-jiantou_qiehuanzuo_o"></use>
            </svg>
          </el-button>
          <el-button type="primary" class="btn">
            <svg class="icon">
              <use xlink:href="#icon-jiantou_qiehuanyou_o"></use>
            </svg>
          </el-button>
        </div>
        <!-- 已绑定的设备 -->
        <div class="Nobinding">
          已绑定的设备
          <el-divider></el-divider>
          <!-- 搜索 -->
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="序列号">
              <el-input v-model="formInline.user" placeholder="支持模糊查询"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
          </el-form>
          <!-- 列表 -->
          <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column
              label="日期"
              width="120">
              <template slot-scope="scope">{{ scope.row.date }}</template>
            </el-table-column>
            <el-table-column
              prop="name"
              label="姓名"
              width="120">
            </el-table-column>
            <el-table-column
              prop="address"
              label="地址"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);
      }
    },

    methods: {
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      onSubmit(){//这是未绑定查询
      },
      handleSelectionChange(){

      }
    },
    data() {
      return {
        filterText: '',
        data: [{
          id: 1,
          label: '一级 1',
          children: [{
            id: 4,
            label: '二级 1-1',
            children: [{
              id: 9,
              label: '三级 1-1-1'
            }, {
              id: 10,
              label: '三级 1-1-2'
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          children: [{
            id: 5,
            label: '二级 2-1'
          }, {
            id: 6,
            label: '二级 2-2'
          }]
        }, {
          id: 3,
          label: '一级 3',
          children: [{
            id: 7,
            label: '二级 3-1'
          }, {
            id: 8,
            label: '二级 3-2'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        formInline:{//这是未绑定的输入框信息
          user:''
        },
        tableData: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-08',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-06',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-07',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }],
        multipleSelection: []
      };
    }
  };
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.bindingBox
  padding 30px
  display flex
  .leftBDing
    width 350px
  .rigthBDing
    flex 1
    margin-left 30px
    display flex
    position relative
    .titBding
      width 100%
      position absolute
      top 0
      left 0
    .Nobinding
      flex 1
      margin-top 70px
    .twoWay
      width 150px
      padding 300px 20px 0
      .btn
        display block
        margin 0 auto
        font-size 24px
        width 90px
        height 40px
        line-height 13px
        margin-bottom 20px
</style>


